@import url(https://fonts.googleapis.com/css2?family=M+PLUS+Rounded+1c:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=M+PLUS+Rounded+1c:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.index_footer{
	position: relative;
	bottom: 0;
	left: 0;
	right: 0;
	height: 5vh;
	background-color: #000000;
}
.index_body{
	background-image: linear-gradient(to bottom, #000000, #000000, 
		#250d18, #400e2d, #5a0e48, #71116a, #76107c, #791390, #771aa6, 
		#6916aa, #5814af, #4114b3, #1716b8, #000000);
	position: relative;
	display: flex;
	flex-direction: row;
	align-items: center;
	width: 100vw;
	overflow-x: hidden;
	justify-content: center;
}

.text{
	font-family: 'M PLUS Rounded 1c', sans-serif;
    color: white;
    position: absolute;
    margin: auto;
    z-index: 5;
    line-height: 1;
	/* margin-left: 28vw; */
}


.title-page-name{
	margin-top: 1vw;
	font-size: 5vw;
	font-weight: 700;
}

.text h2{
	font-size: 3vw;
}

.Raunak-Anand{
	margin-top: -20vw;
	padding: 0.5vw;
	width: 35vw;
	height: auto;
	border-radius: 50vw;
	border: 0.3vw solid white;
	margin-bottom: 2vw;
}

.first-page-logos{
	color: white;
	height: 5vw;
	margin-left: 3vw;
	margin-right: 3vw;
	margin-top: 3vw;
}

#particles-js{
	height: 90vh;
	width: 100vw;
}

@media (min-width:760px) {
	.text{
		/* margin-left: 34vw; */
		margin: auto;

	}
	.title-page-name{
		margin-top: 0vw;
		font-size: 3.5vw;
	}

	.text h2{
		font-size: 2vw;
	}

	.Raunak-Anand{
		width: 20vw;
		margin-top: 0vw;
	}

	.first-page-logos{
		height: 3vw;
		margin: 1vw;
		margin-left: 2vw;
		margin-right: 2vw;
	}

}
.footer{
	display: none;
	background-color: #000000;
	display: flex;
	flex-direction: column;
	padding-bottom: 3vw;
	margin: auto;
	position: static;
  	left: 0;
  	bottom: 0;
  	width: 100%;
}

.col{
	margin: auto;
}

.col-one{
	display: flex;
	flex: row 1;
	align-items: center;
	margin: auto;
}

.footer_logos{
	width: 2.7vw; /*changed this*/
	height: auto;
	margin: 1.5vw;
	color: #ffffff;
	margin-top: 4vw;
}


.footer p{
	font-family: 'M PLUS Rounded 1c';
	color: #ffffff;
	font-size: 1vw;
}

@media (max-width:760px){
	.footer_logos{
		margin-top: 2vw;
	}
	
}
